import React from 'react';
import axios from 'axios';

export default class PersonList extends React.Component {
  state = {
    persons: {}
  }

  componentDidMount() {
    axios.get(`https://localhost/banks`)
      .then(res => {
        const persons = res.data;
        this.setState({ persons });
      })
  }

  render() {
    return (
        <div>
            <h1>Je suis un texte</h1>

            <div className="test"></div>
      <ul>
        {document.querySelector('.test').innerHTML = this.state.persons}
      </ul>
      </div>
    )
  }
}